.works {
    width: 100%;
    background-color: hotpink;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-between;
}

.work {
    background-color: lightblue;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.workContainer {
    display: flex;
    flex-direction: column;
    width: 400px;
    background-color: green;
}

.workImage {
    height: 400px;
    background-color: gray;
    display: flex;
    justify-content: center;
    align-items: center;
}